<template>
  <div>
        <h3 class="text-center">Todas as UFs</h3>
        <div v-if="message" class="alert alert-success">{{this.message}}</div>
        <div class="container">
            <table class="table table-borderless table-striped table-image mt-4">
                <thead>
                    <tr>
                        <th class="text-center">ID</th>
                        <th>Nome</th>
                        <th class="text-center">Sigla</th>
                        <th class="text-center">Editar</th>
                        <th class="text-center">Deletar</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="cidade in cidades" v-bind:key="cidade.id">
                        <td class="text-center">{{cidade.id}}</td>
                        <td>{{cidade.nome}}</td>
                        <td class="text-center">{{cidade.uf.nome}}</td>
                        <td class="text-center"><button class="btn btn-success" v-on:click="update(cidade.id)"><i class="material-icons">edit</i></button></td>
                        <td class="text-center"><button class="btn btn-warning" v-on:click="remove(cidade.id)"><i class="material-icons">delete</i></button></td>
                    </tr>
                </tbody>
            </table>
            <div class="row">
                <button class="btn btn-success" v-on:click="add()">Add</button>
            </div>
        </div>
    </div>
</template>

<script>
import DataService from '../service/CidadeDataService';
export default {
    name: "Cidades",
    data() {
        return {
            cidades: [],
            message: ""
        }
    },
    methods: {
        refresh() {
            DataService.retrieveAll()
            .then((response) => {
                this.cidades = response.data;
            });
        },
        add() {
            this.$router.push(`/cidades/add`);
        },
        update(id) {
            this.$router.push(`/cidades/${id}`);
        },
        remove(id) {
            DataService.remove(id)
            .then(() => {
                this.refresh();
            }).catch(e => {
              console.log(e)
              if(e.response){
                this.$store.dispatch('setSnackbar', {
                  showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
                });
              }
            });
        },
    },
    created() {
        this.refresh();
    }
}
</script>

